import React, {Component, PropTypes} from 'react';
import {connect} from 'react-redux';

class MainFocus extends Component {
  constructor(props) {
    super(props);

    this.state = ({
      inputText: this.props.mainFocus.text || ''
    });

    this.handleInput = this.handleInput.bind(this);
  }

  handleInput(e) {
    this.setState({
      inputText: e.target.value
    });
  }

  render() {
    return (
      <div>
        <div>What is your main focus of today?</div>
        <input type="text" onChange={this.handleInput} value={this.state.inputText}></input>
      </div>
    );
  }
}

MainFocus.propTypes = {
  mainFocus: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    mainFocus: state.mainFocus
  };
};

export default connect(mapStateToProps)(MainFocus);



